@mixin flex()
    display: flex
    justify-content: center
    align-items: center
$color-app: #3a3b3c
$color-odd: #e4cc79
$color-even: #487dc9
$background-cuisine: #5ecbff
*
    margin: 0
    padding: 0
    box-sizing: border-box
    font-size: 16px
    #root
        .header
            box-sizing: border-box
            width: 100%
            height: 56px
            border-bottom: 1px solid #e7e7e7
            @include flex()
            justify-content: space-around
            .navigation-link
                flex: 1 1 0
                text-decoration: none
                text-transform: uppercase
                color: $color-app
                font-size: 20px
                height: 100%
                @include flex()
                justify-content: center
                font-weight: 600
                white-space: nowrap
        
            .active-link
                box-shadow: 0 4px 0px 0px #2e89ff
                color: #2e89ff
        >div
            width: 100%   
        .intro
            height: calc( 100vh - 56px )
            background-image: url("./assets/background.jpg")
            background-clip: border-box
            background-repeat: no-repeat
            background-size: cover
            background-position: center 
        .home
            .member
                @include flex()
                flex-direction: column
                align-items: flex-start
                gap: 10px
                font-size: 20px
                font-weight: 600
                >div
                    &::before
                        list-style-type: disc
                        content: "- "
            padding: 10px 20px
            .no-mean-2
                color: #2e89ff  
                font-size: 24px
                font-weight: 600
                margin-top: 8px
            .list-brief-home
                margin: 8px 0   
            .title-home
                font-size: 32px
                font-weight: 600
            .wrapper
                width: 100%
                @include flex()
                >div
                    flex: 1 1 0
                    &:last-of-type
                        @include flex()
                .left-side
                    .brief-home
                        .brief-home-title
                            font-size: 24px
                            font-weight: 600
                        .brief-home-content
                            font-size: 18px
                            margin: 8px 0
                            font-style: italic
                        .list-brief-home
                            @include flex()
                            flex-direction: column
                            align-items: flex-start
                            gap: 10px
                            font-size: 17px
                            font-weight: 600
                    .no-mean
                        font-size: 28px
                        font-weight: 600
        .overview
        .tourist-attraction
        .schedule
            @include flex()
            padding: 10px
            .detail-schedule-odd
            .detail-schedule-even
                background-color: $color-even
                .time-series-component-do
                    color: #fff !important

            .detail-schedule
                flex: 1 1 0
                @include flex()
                flex-direction: column
                padding: 0 10px
                .day-th
                    width: 100%
                    text-align: center
                    font-size: 24px
                    text-transform: uppercase
                    color: #2e89ff  
                    margin: 0 8px
                    font-weight: 600
                .dayth-odd
                    color: $color-even
                .dayth-even
                    color: $color-odd
                .image-represent
                    margin: 12px 0
                    width: 100%
                    
                    img
                        width: 100%
                        aspect-ratio: 1 / 1
                        object-fit: cover
                        border-radius: 20px
                        overflow: hidden
                .time-series
                    width: 100%
                    @include flex()
                    gap: 12px
                    flex-direction: column
                    .time-series-component
                        width: 100%
                        >span
                            font-weight: 600
                            font-size: 18px
                        .time-series-component-time
                            color:$color-odd
                        .time-series-component-do
                            color:$color-even
        .services
            width: 100%

            .navigation
                width: 100%
                height: 56px
                display: flex
                align-items: center 
                border-bottom: 1px solid #e7e7e7
                .navigation-link
                    flex: 1 1 0
                    text-decoration: none
                    text-transform: uppercase
                    color: $color-app
                    font-size: 20px
                    height: 100%
                    @include flex()
                    justify-content: center
                    font-weight: 600
                    white-space: nowrap
                .active-link
                    box-shadow: 0 4px 0px 0px #2e89ff
                    color: #2e89ff
            .restaurants
                width: 100%
                padding: 0 50px
                .title-restaurants
                    font-size: 36px
                    font-weight: 600
                    margin-bottom: 10px
                .or-restaurants
                    font-size: 24px
                    font-weight: 600
                    margin: 12px 0
                .reverse-class
                    flex-direction: row-reverse !important
                .wrapper
                    margin: 0 8px
                    @include flex()
                    gap: 20px
                    >div
                        flex: 1 1 0
                    .ul-restaurants
                        @include flex()
                        flex-direction: column
                        align-items: flex-start
                        gap: 10px
                        .com-ul-restaurants
                            font-size: 18px
                    .img-restaurants
                        width: 100%
                        img
                            width: 100%
                            aspect-ratio: 16 / 9
                            object-fit: cover
                            border-radius: 15px
        .cuisine-reverse-class
            .place-to-eat
                flex-direction: row-reverse !important
                gap: 10px 
                .list-restaurant-to-eat
                    align-content: flex-end !important
        .cuisine
            padding: 0 50px
            background: $background-cuisine
            color: #e4e6eb
            .intro-province
                .intro-province-name
                    font-weight: 600
                    font-size: 24px
                    margin-bottom: 16px
                .intro-province-brief
                    font-size: 18px
                    font-style: italic
            .get-start-food
                font-size: 32px
                font-weight: 600
                margin: 8px 0
            .food-component
                width: 100%
                .title-food-component
                    font-size: 28px
                    font-weight: 600
                .intro-food-component
                    font-size: 17px
                    margin-top: 8px
                    width: 100%
                    flex-direction: column
                    @include flex()
                    align-items: flex-start
                    gap: 10px
                .no-description
                    font-weight: 600
                    font-size: 18px
                    margin: 8px 0
                .place-to-eat
                    width: 100%
                    @include flex()
                    align-items: flex-start
                    >div
                        flex: 1 1 0
                    .list-restaurant-to-eat
                        font-size: 18px
                        @include flex()
                        flex-direction: column
                        gap: 10px
                        align-items: flex-start
                    .img-restaurant-to-eat
                        img
                            width: 100%
                            aspect-ratio: 16 / 9
                            object-fit: cover
                            border-radius: 15px

        .tour

::-webkit-scrollbar
    width: 8px
::-webkit-scrollbar-track
    border-radius: 10px
::-webkit-scrollbar-thumb
    background: #807d7d
    border-radius: 10px